<template>
    <div>
        <ConsoleHeader />

        <div class="hold-transition login-page">
            <div class="login-box">
                <div class="login-logo">
                <a href="/">
                    <img src="@/assets/logo.jpg" alt="Odi Market Logo" class="img-fluid rounded-circle" style="width: 3em;">
                </a>
                </div>
                <!-- /.login-logo -->
                <div class="card">
                    <div class="card-body login-card-body">
                        <p class="login-box-msg">Console Sign in</p>
                
                        <form name="myForm" method="post" autocomplete="off" @submit.prevent="submitForm">
                        
                        <div class="mb-1 font-weight-bold form-feedback text-danger">
                            {{errorMsg}}
                        </div>
                        <p v-if="showError" class="text-danger font-weight-bold">Username or Password is incorrect</p>

                        <div class="input-group mb-3">
                            <input type="email" name="email" v-model="form.email" class="form-control" placeholder="Email" required />
                            <div class="input-group-append">
                                <div class="input-group-text">
                                    <span class="mdi mdi-envelope"></span>
                                </div>
                            </div>
                        </div>
                        
                        
                        <div class="input-group mb-3">
                            <input type="password" name="password" v-model="form.password" class="form-control" placeholder="Password" required />
                            <div class="input-group-append">
                            <div class="input-group-text">
                                <span class="fas fa-lock"></span>
                            </div>
                            </div>
                        </div>
                    
                        <!-- <div class="icheck-primary float-left">
                            <input type="checkbox" id="remember">
                            <label for="remember">
                            Remember Me
                            </label>
                        </div> -->
                        
                            

                        <div class="social-auth-links text-center mb-3" >
                            <!-- <p>- OR -</p> -->
                            <button type="submit" class="btn btn-success btn-block">Sign In</button>
                            <!-- <a href="#" class="btn btn-block btn-primary">
                            <i class="fab fa-facebook mr-2"></i> Sign in using Facebook
                            </a> -->
                            <!-- <a href="#" class="btn btn-block btn-danger">
                            <i class="fab fa-google-plus mr-2"></i> Sign in using Google+
                            </a> -->
                        </div>
                        </form>
                
                        <!-- /.social-auth-links -->
                
                        <!-- <p class="mb-1">
                        <a href="forgot-password.html">I forgot my password</a>
                        </p> -->
                        <!-- <p class="mb-0">
                        <a href="/register" class="text-center">Register a new membership</a>
                        </p> -->
                    </div>
                <!-- /.login-card-body -->
                </div>
            </div>
        </div>

        <ConsoleFooter />
    </div>
</template>


<script>
import ConsoleHeader from "./layouts/ConsoleHeader.vue";
import ConsoleFooter from "./layouts/ConsoleFooter.vue";

import {mapGetters, mapActions} from 'vuex';

export default {
    name: "ConsoleLogin",
    data(){
        return{
            form: {
                email: '',
                password: '',
            },
            showError: false
        }
    },
    computed: mapGetters(['errorMsg']),
    components: {
        ConsoleHeader,
        ConsoleFooter
    },
    methods: {
        ...mapActions(['login']),

        async submitForm(){

            
            const User = new FormData();
            User.append("email", this.form.email);
            User.append("password", this.form.password);
    
            try{
                await this.login(User);
                this.$router.push("/console");
                
            }
            catch(err){
                console.log(err);
            }

        }
    }
}
</script>

<style scoped>
    
</style>